import Vue from 'vue'
import App from './App.vue'
import { router } from './router';
// import lib-flexible
import 'amfe-flexible'

import store from "./store/index";
import PluginAdapter from "./pluginManager/pluginAdapter";
import echarts from 'echarts'
// vant
import Vant from 'vant';
import 'vant/lib/index.css';
import { storage } from './utils/storage'
import { Lazyload } from 'vant'
//RSA
import JSEncrypt from 'jsencrypt';
// import VueAMap from 'vue-amap';
Vue.prototype.$jsencrypt = JSEncrypt;

import localImg from './utils/localImg'; //

// options 
Vue.use(Lazyload, {
  error: require('@/assets/images/default/default_picture.png'), // 
  loading: require('@/assets/images/default/default_picture.png') // 
});
// Vue.use(VueAMap);
// VueAMap.initAMapApiLoader({
//   key: '529ff32129046a07fd11ce5a2054a797',
//   plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor'],
//   //  sdk  1.4.4
//   v: '1.4.4'
// });
Vue.use(Vant);
Vue.config.productionTip = false;
Vue.config.bus = new Vue();
new PluginAdapter(function (pluginMap) {
  Vue.prototype.$plugin = pluginMap
  // echarts
  Vue.prototype.$echarts = echarts
  //
  Vue.prototype.$isLogin = false;
  //(YouPin)(order)(coupon)(address)(cart)
  Vue.prototype.$enterPage = window.location.href.split('=')[1];
  //
  Vue.prototype.$setVirtualArea = true;
  //
  Vue.prototype.$setUserAreaInfo = {
    "buildName": "6",
    "unitNo": 0,
    "roomNo": "",
    "cityCode": "2018012400001",
    "parentDeptCode": "125",
    "roomCode": "2020032600003",
    "buildCode": "2020032600002",
    "roomName": "6",
    "areaCode": "2020032600001",
    "cityName": "",
    "areaName": "",
    "areaNo": "",
    "relateFlag": "0",
    "floorNo": 1,
    "deptCode": "125"
  }
  //{}
  Vue.prototype.$refreshHomePage = false;
  //
  Vue.prototype.$userInfo = null;
  //
  Vue.prototype.$loginInfo = null;
  // 
  Vue.prototype.$fromPage = '';
  //
  Vue.prototype.$isShowKeyboard = false;

  //  userInfo setStoragestorage
  Vue.prototype.$setUserInfo = function (userInfo, updateStorage = true) {
    //
    if (userInfo == undefined
      || userInfo == null) {
      Vue.prototype.$isLogin = false;
      Vue.prototype.$userInfo = null;
      if (updateStorage) {
        //
        storage.deleteStorage('userInfo');
      }
    } else {
      //
      Vue.prototype.$isLogin = true;
      Vue.prototype.$userInfo = userInfo;
      if (updateStorage) {
        //localStorage
        storage.setStorage('userInfo', userInfo, 60);
      }
    }
  };
  //
  Vue.prototype.$setLoginInfo = function (loginInfo, updateStorage = true) {
    Vue.prototype.$loginInfo = loginInfo;
    if (updateStorage) {
      //localStorage
      storage.setStorage('loginInfo', loginInfo, 60);
    }
  }
  //
  Vue.prototype.$setRouterIndex = function () {
    let curUrl = window.location.href;
    let arrUrl = curUrl.split("#");
    let pageUrl = arrUrl[1];
    //
    if (pageUrl == '/myOrderList?enter=true' || pageUrl == '/couponList' || pageUrl == '/myCollect' || pageUrl == '/prizeRecord' || pageUrl == '/myPoints?enter=true' || pageUrl == '/afterSaleList'|| pageUrl == '/shippingAddressList?flag=1' || pageUrl == '/shoppingCart?enter=true' || pageUrl == '/goodsDetail?enter=true' || pageUrl == '/shopPage?enter=true') {
      console.log("");
      var paramsData = { encoder: 'base64' };
      var pid = 666;
      paramsData = JSON.stringify(paramsData);
      if (this.$devicePlatform == "ios") {
        //ios
        window.webkit.messageHandlers.JSCtrl.postMessage(['send_backSignal_xinwojia', pid, paramsData])
      } else if (this.$devicePlatform == "android") {
        //android
        window.store.send_backSignal_xinwojia(pid, paramsData);
      }
      return false;
    } else {
      console.log("");
      return true;
    }
  }
  //0
  Vue.prototype.$stbHeight = 0;
  //, default:light:
  Vue.prototype.$curStbStyle = "default";

  //0
  Vue.prototype.$footBarHeight = 0;

  //
  Vue.prototype.$setStbLightStyle = function () {
    Vue.prototype.$curStbStyle = "light";
    //console.log("setStbLightStyle: stbHeight = " + this.$stbHeight + ", platform = " + this.$devicePlatform);
    if (this.$stbHeight > 0 && this.$devicePlatform != 'pc') {
      StatusBar.styleLightContent();
    }
  };

  //
  Vue.prototype.$setStbDefaultStyle = function () {
    Vue.prototype.$curStbStyle = "default";
    //console.log("setStbDefaultStyle: stbHeight = " + this.$stbHeight + ", platform = " + this.$devicePlatform);
    if (this.$stbHeight > 0 && this.$devicePlatform != 'pc') {
      StatusBar.styleDefault();
    }
  };

  //
  Vue.prototype.$updateStb = function (to) {
    console.log("updateStb: stbHeight = " + this.$stbHeight + ", platform = " + this.$devicePlatform);
    if (this.$stbHeight > 0 && this.$devicePlatform != 'pc') {
      /*if (cordova.platformId == 'android') { //  cordova 
        console.log("updateStb: ");
        StatusBar.overlaysWebView(true);
      }*/
      if (to.meta.statusBgc) {
        if (Vue.prototype.$curStbStyle != "light") {
          console.log("updateStb: ");
          StatusBar.styleLightContent();
          Vue.prototype.$curStbStyle = "light";
        } else {
          console.log("updateStb: ");
        }
      } else {
        if (Vue.prototype.$curStbStyle != "default") {
          console.log("updateStb: ");
          StatusBar.styleDefault();
          Vue.prototype.$curStbStyle = "default";
        } else {
          console.log("updateStb: ");
        }
      }
      //to.meta.statusBgc ? StatusBar.styleLightContent() : StatusBar.styleDefault()

    }
  };

  //0: 1
  Vue.prototype.$curTheme = "0";

  //
  Vue.prototype.$openVirArea = true;

  //
  Vue.prototype.$needUpOwner = false;

  Vue.filter('keepTwoNum', function (value) {
    value = Number(value);
    return value.toFixed(2);
  });

  new Vue({
    router,
    store,
    render: h => h(App),
  }).$mount('#app')
});

// //jsrsasign
Vue.prototype.$getRSASign = function (password, key) {
  let sign = new JSEncrypt({
    default_key_size: 1024
  });
  key = key.replace("/r", "");
  console.log(key);
  key = "-----BEGIN PRIVATE KEY-----" + key + "-----END PRIVATE KEY-----"
  sign.setPublicKey(key);
  let data = sign.encrypt(password.toString());
  return data;
};



