import http from "../http";


export { getVoiceDiscernService, getVoiceErrorCorrectService }

// 
const getVoiceDiscernService = (params) => {
    return new Promise((resolve, reject) => {
        let reqUrl = 'cloudbrain-service-gateway/assistant/intentionparse';
        http.get(reqUrl , params, 'baseUrl').then(result => {
            result.resultCode == '0' ? resolve(result.data) : reject(result.msg);
        }).catch(error => {
            reject(error);
        })
    });
}

